
import LoadMore from 'vux/src/components/load-more/index.vue'

export default {
name: 'LingLoading',
components: {
LoadMore
},
props: {
state: {
type: Object,
require: true
},
click: {
type: Function
}
},
methods: {
/**
* 只有当获取数据失败的时候才能在Tap时重新获取数据
*/
handleClick() {
if(this.state.state == 'error' && this.click) {
this.click()
}
return
}
}
}



