export default {
  /**
   * 更新状态
   * @param state
   * @param loading 需要更新的状态（可部分更新）
   */
  changeLingLoading (state, loading) {
    state.lingLoading = {...state.lingLoading, ...loading}
  },
  changeLoading (state, isLoading) {
    state.isLoading = isLoading
  }
}



